import * as React from 'react'
import styled from '@emotion/styled'

import { Link, navigate } from 'gatsby'
import Img from "gatsby-image"

import { heights, dimensions, colors } from '../styles/variables'

const StyledCard = styled.div`
background-color: ${colors.ui.whisper};
border-radius: .4rem;
overflow: hidden;
box-shadow: 0 3rem 6rem rgba(0, 0, 0, .1);
margin: 5rem 0rem .3rem 0rem;
`


const HeaderImage = styled(Img)`
max-height: 20rem;
object-fit: cover;

picture>img {
  transition: all 0.4s!important;
}
`;

const StyledTitle = styled.h1`
@media (max-width: 768px) {
font-size: 1.5rem;
}

font-size: 2.5rem;
font-weight: 500;
transition: .3s all;
color: ${colors.brand};
`;

const BlogPostInfo = styled.div`
color: ${colors.black};
display: flex;
justify-content: flex-end;
font-weight:400;
`;

const InnerCard = styled.div`
padding: 2rem;

@media (max-width: 768px) {
  padding: 1rem;
  }
`;


const Tag = styled.div`
color: ${colors.white};
border: none;
border-radius: .4rem;
display: block;
background-color: ${colors.brand};
margin: .25rem;
padding: .1rem .5rem;
cursor: pointer;


&:hover {
  background-color: ${colors.accent};
  color: ${colors.brand};
  text-decoration: none;
}
`;

const InnerInfo = styled.div`
flex-direction: column;
`;

const Tags = styled.div`
flex-flow: wrap;
padding-bottom: 2rem;
display: flex;

& > div {
transition: .3s all;
}

`;

interface ContainerProps {
  className?: string
  card: any
}

const Card: React.FC<ContainerProps> = ({ card, children, className }) => {
  return <StyledCard className={className}>
    <HeaderImage fluid={card.frontmatter.headerImg.childImageSharp.fluid} alt="" />
    <InnerCard className="InnerCard">
      <StyledTitle>{card.frontmatter.title}</StyledTitle>
      <BlogPostInfo>
        <InnerInfo>
          <div>
            {card.frontmatter.date} &#183; {card.timeToRead} min reading
            </div>
          <div>
            <a target="_blank" rel="noreferrer" href="{card.frontmatter.postedAt.url}">Originally posted at {card.frontmatter.postedAt.name}</a>
          </div>
        </InnerInfo>
      </BlogPostInfo>
      <Tags>
        {card.frontmatter.tags.map((value: string) => {
          return <Tag onClick={(event) => {
            event.preventDefault();
            navigate(`/blog/tags/${value}`)
          }} key={value}>{value}</Tag>
        })}
      </Tags>
      {children}
    </InnerCard>
  </StyledCard >
}

export default Card
